<template>
  <div class="showBox">
    <section class="grid-stack"></section>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import { useStore } from "vuex";
// import Chart from "chart.js/auto";
import { GridStack } from "gridstack";
import "gridstack/dist/h5/gridstack-dd-native";
export default {
  name: "ShowBox",
  props: {},
  setup() {
    const store = useStore();
    let grid = null;
    let items = [];
    var myChart = ref(null);
    onMounted(() => {
      items = store.state.box_item.map((item) => item);

      grid = GridStack.init({
        float: true,
        minRow: 1,
        minWidth: 500,
        column: 12,
        cellHeight: "80px",
      });

      grid.on("dragstop", () => {
        // (event, element)
        // const node = element.gridstackNode;
        // console.log(grid.engine.nodes);

        items.forEach((box) => {
          const new_node = grid.engine.nodes.filter((item) => box.id === item.id)[0];
          box.x = new_node.x;
          box.y = new_node.y;
        });
        store.dispatch("sava_box_data", items);
      });

      items.forEach((element) => {
        add_new_widget(element);
      });
    });

    const add_new_widget = (item) => {
      const node = item;
      grid.addWidget(node);
    };

    return {
      // add_new_widget,
      myChart,
    };
  },
};
</script>

<style>
@import "../../node_modules/gridstack/dist/gridstack.min.css";

