  position: sticky;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.card table {
  margin: 5px auto;
  border-collapse: collapse;
  width: 90%;
}
.card table,
.card tr,
.card td {
  border-bottom: 1px solid gray;
